	module.exports = {"data":{"post":{"html":"<p>Yet another difference between Germany and the US. Germany sells high tech to China that it can't afford itself (Transrapid train for Shanghai). The US just drops high tech espionage aircraft at China's doorstep... at no charge.</p>\n<p>And another. Bush rejects the Kyoto accord and gets raspberries from home and abroad. Germany's Schröder embraces Kyoto, yet according to ARD's <a href=\"http://www.monitor.de\">Monitor</a> German carbon dioxide emissions actually increased in 2000. </p>\n<p>And on the home front, Christopher is once again very 'consistent', to the relief of both parents and day care.  (By the way, his new favorite toys are Happy Meal premiums Furby and Shelby that sing to one another. Hopefully the batteries will run out soon.) </p>\n<p>Bovine PC. At work, I was given a PC named 'galloway' as a backup web server. It's now infected with BSE... I mean, installed with BSD. I then installed the ports, and now it's full of MK files (MK is the German abbreviation for Foot and Mouth). I hope that a disinfection will not be necessary.</p>","fields":{"title":"Oh my God, can't say no","date":"05 Apr 2001","url":"https://papascott.de/2001-04-05-oh-my-god-cant-say-no/"}}},"pathContext":{"slug":"/2001-04-05-oh-my-god-cant-say-no/","prev":{"fields":{"title":"In my world I take him out for tea","date":"02 Apr 2001","slug":"/2001-04-02-in-my-world-i-take-him-out-for-tea/"}},"next":{"fields":{"title":"Skipping school, gotta walk for air","date":"08 Apr 2001","slug":"/2001-04-08-skipping-school-gotta-walk-for-air/"}}}}

